import React, { useState, useEffect } from "react";

const Checkbox = ({ selected, setSelected }) => {
  const [check, setCheck] = useState(false);

  const handleOnChange = (e) => {
    setCheck(true)
    var cbs = document.querySelectorAll(".cb")
    cbs.forEach((cb) => {
      if(cb !== e) cb.checked = false
    });


    // console.log("hello1 ", e.target.value);
    // console.log("hello2 ", e);
  };

  return (
    <div>
      <input
        type="radio"
        className={`cb`}
        onChange={handleOnChange}
        checked={check}
      ></input>
    </div>
  );
};

export default Checkbox;





