import "../node_modules/bootstrap/dist/css/bootstrap.min.css"
import "../node_modules/bootstrap/dist/js/bootstrap.min.js"
import "./App.css";
import { useState } from "react";
import Header from "./components/Header";
import Sidebar from "./components/Sidebar";
import PrevHistSrchTable from "./components/PrevHistSrchTable";
import RightSection from "./components/RightSection";

const App = () => {
  const [clicked, setClicked] = useState('');
  const [srchInput, setSrchInput] = useState('')
  const [srchOption, setSrchOption] = useState('select')
  const [selected, setSelected] = useState(null)

  let i = 0;
  setInterval(() => {
    document.title = i++ % 2 === 0 ? "Rakbank" : "TWC - Copy Profile"
  }, 3000) 

  return (
    <div>
      <Header />
      <div id="rest"> 
        <div id="mainContent">
          <Sidebar clicked={clicked} setClicked={setClicked} srchInput={srchInput} srchOption={srchOption} setSrchInput={setSrchInput} setSrchOption={setSrchOption}/> 
          <RightSection clicked={clicked} setClicked={setClicked} srchInput={srchInput} srchOption={srchOption} setSrchInput={setSrchInput} setSrchOption={setSrchOption} selected={selected} setSelected={setSelected} /> {/* toDo*/ }
        </div>

        <PrevHistSrchTable />
      </div>
    </div>
  );
};

export default App;
