import "../App.css";
import rakbankLogo from "../rakbank_logo.jpg";

const Header = () => {
  return (
    <div id="header">
      <div id="left">
        <img id="logo" className="shadow" src={rakbankLogo} alt="" />
      </div>
      <div id="mid" className="shadow-sm rounded-2 mx-2 user-select-none">
        <h2>
          <strong>TWC- Copy Profile</strong>
        </h2>
      </div>
      <div id="right" className="doCenter">
        <div className="headerIP col-sm-10">
          <label
            htmlFor="inputPassword"
            className="col-sm-2 col-form-label headerIPLbl"
          >
            Logged-in User:
          </label>
          <input className="form-control inputBox shadow" id="inputBox" disabled/>
        </div>

        <div className="headerIP col-sm-10">
          <label
            htmlFor="inputPassword"
            className="col-sm-2 col-form-label headerIPLbl"
          >
            Reference Number:
          </label>
          <input className="form-control inputBox shadow" id="inputBox" disabled/>
        </div>
      </div>
    </div>
  );
};

export default Header;
