import "../App.css";
import Table from './Table.js'
import Temp from './Temp.js'

const RightSection = ( props ) => {

  return (
    <> 
      <div id="rightSection" className="doCenter"> 
        {props.clicked && <Table setClicked={props.setClicked} srchOption={props.srchOption} srchInput={props.srchInput} setSrchInput={props.setSrchInput} setSrchOption={props.setSrchOption} selected={props.selected} setSelected={props.setSelected}/>}
      </div>
    </>
  );
};

export default RightSection;