import { useState, useEffect, useMemo } from "react";
import { useTable, usePagination, useRowSelect } from "react-table";
import "../App.css";
import { COLS } from "./Columns1";
import Checkbox from "./Checkbox";
import ProgressBar from "react-bootstrap/ProgressBar";

const Table = ({
  clicked,
  setClicked,
  srchInput,
  srchOption,
  setSrchInput,
  setSrchOption,
  selected,
  setSelected,
}) => {
  const [isLoading, setIsLoading] = useState(true);

  const columns = useMemo(() => COLS, []);

  var newans = [];
  function ajaxCall() {
    var params = `EventType=onClick&srchOption=${srchOption}&srchInput=${srchInput}`;
    var url = "/webdesktop/CustomForms/TWC_CopyProfile/TWC_CopyProfile.jsp";
    // var url = "https://api.sampleapis.com/coffee/hot";
    var xhr;
    var ajaxResult;

    if (window.XMLHttpRequest) xhr = new XMLHttpRequest(); // chrome/Edge
    else if (window.ActiveXObject)
      xhr = new window.ActiveXObject("Microsoft.XMLHTTP"); // IE 5 or 6

    xhr.open("GET", url, false);
    xhr.send(params);

    if (xhr.status === 200) {
      ajaxResult = xhr.responseText; // result
      // ajaxResult = "1111~Under Approval~12345~abcDef~Finance Approved~somedate~TWC~Someone~Omni-flow#1111~Under Approval~12345~abcDef~Finance Approved~3rd-February 2023~TWC~Someone~Omni-flow#1111~Under Approval~12345~abcDef~Finance Approved~3rd-February 2023~TWC~Someone~Omni-flow#1111~Under Approval~12345~abcDef~Finance Approved~3rd-February 2023~TWC~Someone~Omni-flow#1111~Under Approval~12345~abcDef~Finance Approved~3rd-February 2023~TWC~Someone~Omni-flow#1111~Under Approval~12345~abcDef~Finance Approved~3rd-February 2023~TWC~Someone~Omni-flow#1111~Under Approval~12345~abcDef~Finance Approved~3rd-February 2023~TWC~Someone~Omni-flow";
      console.log('ajaxResult --> ', ajaxResult) // JSON.parse(ajaxResult)[0].title 

      if (!ajaxResult) {
        setTimeout(() => {
          setClicked("");
          setSrchInput("");
          setSrchOption("select");
        }, 100);
        ajaxResult = " ~ ~ ~ ~ ~ ~ ";
      }

      var arr = ajaxResult.split("#");

      newans = []; // 2d
      for (let i = 0; i < arr.length; i++){
        newans[i] = arr[i].split("~");
      }

      const labels = [
        "wi_number",
        "cif_id",
        "tl_number",
        "customer_name",
        "wi_status_stage",
        "wi_created_date_time",
        "wi_completed_date_time",
        "initiated_channel",
        "case_environment",
      ];

      newans = newans.map((subarr) => {
        const obj = {};

        subarr.forEach((e, i) => {
          obj[labels[i]] = e;
        });
        return obj;
      });

      setTimeout(() => {
        setIsLoading(false);
      }, 1500);

      return newans;
    } else {
      alert("Error while handling for the current workstep");
      return "";
    }
  }

  var prevData;
  useState(() => {
    // New way instead of useEffect
    prevData = ajaxCall();
  }, []);

  const data = useMemo(() => prevData, []);

  const {
    getTableProps,

    getTableBodyProps,

    headerGroups,

    page,

    nextPage,

    setPageSize,

    previousPage,

    canNextPage,

    canPreviousPage,

    pageOptions,

    state,

    prepareRow,
  } = useTable(
    {
      columns,
      data,
    },
    usePagination,
    useRowSelect,
    (hooks) => {
      hooks.visibleColumns.push((columns) => {
        return [
          {
            id: "selection",
            Header: ({ getToggleAllRowsSelectedProps }) => {
              <Checkbox
                selected={selected}
                setSelected={setSelected}
                {...getToggleAllRowsSelectedProps()}
              />;
            },
            Cell: ({ row }) => (
              <Checkbox
                selected={selected}
                setSelected={setSelected}
                {...row.getToggleRowSelectedProps()}
                style={{ width: "15px" }}
              />
            ),
          },
          ...columns,
        ];
      });
    }
  );

  const { pageIndex } = state; // pageIndex destrucutred from state of the TableState<Object>

  useEffect(() => {
    setPageSize(5);
  }, []);

  const [progress, setProgress] = useState(0);

  useEffect(() => {
    const intervalId = setInterval(() => {
      setProgress((progress) => {
        if (progress >= 100) {
          clearInterval(intervalId);
          return 100;
        }
        return progress + 1;
      });
    }, 9); // Increment progress every 20 milliseconds (2 seconds / 100)

    return () => clearInterval(intervalId);
  }, []);

  return (
    <>
      {isLoading ? (
        <div style={{ width: "100%", height: "50px" }}>
          <ProgressBar animated now={progress} label="Loading.." />
        </div>
      ) : (
        <div id="tableCont">
          <div id="rightSectionTable">
            <table id="mainTable" className="" {...getTableProps()}>
              <thead>
                {headerGroups.map((headerGroup) => {
                  return (
                    <tr {...headerGroup.getHeaderGroupProps()}>
                      {headerGroup.headers.map((column) => (
                        <th {...column.getHeaderProps()}>
                          {column.render("Header")}
                        </th>
                      ))}
                    </tr>
                  );
                })}
              </thead>

              <tbody {...getTableBodyProps()}>
                {page.map((row) => {
                  prepareRow(row);
                  return (
                    <tr {...row.getRowProps()}>
                      {row.cells.map((cell) => {
                        return (
                          <td {...cell.getCellProps()}>
                            {cell.render("Cell")}{" "}
                          </td>
                        );
                      })}
                    </tr>
                  );
                })}
              </tbody>
            </table>
          </div>
          <div id="belowRightSectionTable">
            <span>
              Page{" "}
              <strong>
                {pageIndex + 1} of {pageOptions.length}
              </strong>{" "}
            </span>

            <button
              className="btn btn-secondary btn-sm"
              style={{ width: "90px", margin: "5px" }}
              onClick={() => previousPage()}
              disabled={!canPreviousPage}
            >
              {" "}
              Previous
            </button>

            <button
              className="btn btn-secondary btn-sm"
              style={{ width: "90px", margin: "5px" }}
              onClick={() => nextPage()}
              disabled={!canNextPage}
            >
              Next
            </button>

            <button
              id="submit"
              type="button"
              className="btn btn-success btn-sm"
              style={{ width: "90px", margin: "5px" }}
            >
              Submit
            </button>
          </div>
        </div>
      )}
    </>
  );
};

export default Table;
